import React from "react";
import Typography from "@mui/material/Typography"
import { useState } from "react";
import { CSSTransition } from "react-transition-group";
import Alert from "react-bootstrap/Alert";
import Button from "react-bootstrap/Button";

const GlobalMining = ({ mining }) => {
  const [showButton, setShowButton] = useState(true);
  const [showMessage, setShowMessage] = useState(false);

  //this is the setup for the Global Mining Sites button.
  return (
    <div>
          <img className="earth rotate" src={require("../img/earth.jpeg")} width="40" height="40"/>
      {showButton && (
        <Button
          onClick={() => setShowMessage(true)}
          size="sm"
          className="global-mining-button"
        ><Typography variant="overline">Global Mining Sites</Typography>
        </Button>
      )}
      <CSSTransition
        in={showMessage}
        timeout={200}
        classNames="alert"
        unmountOnExit
        onEnter={() => setShowButton(false)}
        onExited={() => setShowButton(true)}
      >
        <Alert
          className="alert"
          variant="primary"
          onClose={() => setShowMessage(false)}
        >
          <Alert.Heading className="alert-heading">
            Global Mining Sites:
          </Alert.Heading>
          
          <Typography className="mining" variant="overline"><strong>{mining}</strong></Typography>
          <Button
            className="close-button"
            variant="sm"
            onClick={() => setShowMessage(false)}
          ><Typography variant="overline"> Close</Typography>
          </Button>
        </Alert>
      </CSSTransition>
    </div>
  );
};

export default GlobalMining;
