import React from "react";
import smoky from "../img/smokyquartzresize.jpg";
import obsidian from "../img/Obsidianresize.jpg";
import malachite from "../img/malachite01.jpg";
import tigers from "../img/tigerseyeresize.jpg";
import lapis from "../img/lazuliresize.jpg";
import rose from "../img/rose-quartz.jpg";

//create component for Pagination
const Pagination = ({ cardsPerPage, totalCards, paginate, setAppearCard, url}) => {
  const pageNumbers = [];
  const colors = ['#0088FE', '#00C49F', '#FFBB28', '#FF8042', '#FFBB28', '#0088FE'];
  const imgs = [smoky, obsidian, malachite, tigers, lapis, rose]


  for (let i = 1; i <= Math.ceil(totalCards / cardsPerPage); i++) {
    pageNumbers.push(i);
  }

const buttonImg = {url};

  return (
    <nav className="pagination">
      <ul className="pagination">
        {pageNumbers.map((a, i) => (
          <li key={a} className="page-items">
            <div
              onClick={() => paginate(a)}
              type="button"
              className="page-link"
              style={{backgroundImage: "url(" + imgs[i] + ")", backgroundRepeat: "no-repeat", backgroundPosition: "center", backgroundSize: "cover" }}
            >
          
            </div>
          </li>
        ))}
      </ul>
    </nav>
  );
};

export default Pagination;
