// import React, { useState, useEffect } from "react";
import React from "react";
import Cards from "./Cards.js";
import Pagination from "./Pagination.js";
import { CSSTransition } from "react-transition-group";

//this is the comnponent that displays one crystal card at a time
function CrystalDisplay({ props }) {
  const [crystals, setCrystals] = React.useState([]);
  const [curCard, setCurCard] = React.useState(1);
  const [cardsPerPage] = React.useState(1);
  const [appearCard, setAppearCard] = React.useState();

  //retrieve json data and pass as props
  React.useEffect(() => {
    fetch("./crystals.json")
      .then((res) => res.json())
      .then((dataObject) => {
        setCrystals(dataObject);
      });
  }, []);




  //Get current cards
  const indexOfLastCard = curCard * cardsPerPage;
  const indexOfFirstCard = indexOfLastCard - cardsPerPage;
  const currentCards = crystals.slice(indexOfFirstCard, indexOfLastCard);

  //Change page
  const paginate = (pageNumber) => setCurCard(pageNumber) + setAppearCard(true);

  //render individual crystal card with pagination
  return (
    <div className="Background">
      <CSSTransition
        in={appearCard}
        appear={true}
        timeout={1000}
        classNames="fade"
      >
        <Cards crystals={currentCards} />
      </CSSTransition>
    
      <Pagination
        url={crystals.url}
        cardsPerPage={cardsPerPage}
        totalCards={crystals.length}
        paginate={paginate}
      />
    </div>
  );
}

export default CrystalDisplay;
