import React from "react";
import { Card, CardContent, Typography } from "@mui/material";
import GlobalMining from "./GlobalMining.js";
import ParticleComponent from './ParticleComponent';
import Modal from './Modal.js';


const CardLayout = ({ name, meta, healing, purpose, url, mining, physical}) => {
  
  //this is the crystal card setup. it retrieves data through props
  return (
    <div className="App">
      <ParticleComponent theme=''/>
        <Card
          className="Card"
          raised="true"
          variant="outlined"
          sx={{ minWidth: 650}}
        >
          <CardContent>

            <h4 className="title">{name}</h4>
            <Typography variant="h5" mb="10px" color="text.secondary">
              {purpose}
            </Typography>
           <Modal url={url} physical={physical} hardness={physical.hardness} composition={physical.composition} family={physical.family} luster={physical.luster}/>
            <Typography
              className="paragraph"
              paragraph="true"
              variant="overline"
              align="left"
            >
              {healing}
            </Typography>
            <div className="global-mining-container">
              <GlobalMining mining={mining} />
            </div>
            <div>
              <div className="table" raised="true" variant="outlined">
                <tr>
                  <td>Zodiac:</td>
                  <td>
                    <Typography variant="subtitle12">{meta.sign}</Typography>
                  </td>
                </tr>
                <tr>
                  <td>Planet:</td>
                  <td>
                    <Typography variant="subtitle12">{meta.planet}</Typography>
                  </td>
                </tr>
                <tr>
                  <td>Element:</td>
                  <td>
                    <Typography variant="subtitle12">{meta.element}</Typography>
                  </td>
                </tr>
                <tr>
                  <td>Chakra:</td>
                  <td>
                    <Typography variant="subtitle12">{meta.chakra}</Typography>
                  </td>
                </tr>
              </div>
            </div>
          </CardContent>
        </Card>
      </div>

  );
};

export default CardLayout;
