import React from "react";
import CardLayout from "./CardLayout.js";
import { CSSTransition, TransitionGroup } from "react-transition-group";

const Cards = ({ crystals }) => {
  //this recieves parsed crystal data from CrystalDisplay and passes props to CardLayout
  return (
    <div className="mb-4">
      {crystals.map((crystal, key) => (
        <TransitionGroup className="card-container">
          <CSSTransition key={crystals.id} timeout={1000} classNames="slide">
            <CardLayout
              id={key}
              name={crystal.name}
              purpose={crystal.purpose}
              url={crystal.url}
              meta={crystal.meta}
              sign={crystal.meta.sign}
              planet={crystal.meta.planet}
              notes={crystal.meta.notes}
              healing={crystal.healing}
              mining={crystal.mining}
              physical={crystal.physical}
              hardness={crystal.physical.hardness}
              composition={crystal.physical.composition}
              family={crystal.physical.family}
              luster={crystal.physical.luster}
              setAppearCard={true}
            />
          </CSSTransition>
        </TransitionGroup>
      ))}
    </div>
  );
};

export default Cards;
